import { Component, Pipe, PipeTransform, Input } from '@angular/core';

import DataSource from 'devextreme/data/data_source';
import { DxedatagridService } from './dxedatagrid.service';


@Component({
    selector: 'crem-datagrid',
    template: `<dx-data-grid id="gridContainer"
    [dataSource]="dataSource"
    [remoteOperations]="true"
    [allowColumnReordering]="true"
    [rowAlternationEnabled]="true"
    [showBorders]="false"
    (onContentReady)="contentReady($event)">

    <dxo-paging [pageSize]="pagesize"></dxo-paging>
    <dxo-pager
        [showPageSizeSelector]="true"
        [allowedPageSizes]="[10, 25, 50, 100]"
    ></dxo-pager>
    <dxo-search-panel
        [visible]="true"
        [highlightCaseSensitive]="true"
    ></dxo-search-panel>
    <dxo-group-panel
        [visible]="true"
    ></dxo-group-panel>
    <dxo-grouping
        [autoExpandAll]="false"
    ></dxo-grouping>

    <dxi-column dataField="Product" [groupIndex]="0"></dxi-column>
    <dxi-column dataField="Amount" caption="Sale Amount" dataType="number" format="currency" alignment="left"></dxi-column>
    <dxi-column caption="Discount %" dataField="Discount" cellTemplate="discountCellTemplate" dataType="number" format="percent" alignment="left" [allowGrouping]="false" cssClass="bullet"></dxi-column>
    <dxi-column dataField="SaleDate" dataType="date"></dxi-column>
    <dxi-column dataField="Region" dataType="string"></dxi-column>
    <dxi-column dataField="Sector" dataType="string"></dxi-column>
    <dxi-column dataField="Channel" dataType="string"></dxi-column>
    <dxi-column dataField="Customer" dataType="string"></dxi-column>

    <div *dxTemplate="let d of 'discountCellTemplate'">
          <div class="chart-cell">
              <dx-bullet
                  [showTarget]="false"
                  [showZeroLevel]="true"
                  [startScaleValue]="0"
                  [endScaleValue]="100"
                  [value]="d.value * 100">
                  <dxo-size [width]="150" [height]="35"></dxo-size>
                  <dxo-tooltip
                      [enabled]="true"
                      [paddingTopBottom]="2"
                      [customizeTooltip]="customizeTooltip">
                      <dxo-font [size]="18"></dxo-font>
                  </dxo-tooltip>
                  <dxo-margin
                      [top]="5"
                      [bottom]="0"
                      [left]="5"
                  ></dxo-margin>
              </dx-bullet>
          </div>
      </div>
</dx-data-grid>`,
    styleUrls: ['dxedatagrid.component.scss']
})
export class DxedatagridComponent {
  @Input() dataSource: DataSource;
  @Input() collapsed : boolean;
  @Input() pagesize: number;
    contentReady = (e) => {
        if(!this.collapsed) {
            this.collapsed = true;
            e.component.expandRow(['EnviroCare']);
        }
    };
    customizeTooltip = (pointsInfo) => {
        return { text: parseInt('', pointsInfo.originalValue) + '%' };
    }

    constructor(service: DxedatagridService) {
        this.dataSource = service.getDataSource();
    }
}
